'use client';
//ImagesPage.tsx
import { useState, useRef } from 'react';
import { HiUpload, HiX, HiDownload, HiTrash, HiChevronLeft, HiPhotograph } from 'react-icons/hi';
import { BsImages, BsFileEarmarkImage } from 'react-icons/bs';
import Image from 'next/image';

interface UploadedImage {
  id: string;
  file: File;
  preview: string;
  uploadedAt: Date;
}

interface ImagesPageProps {
  onClose: () => void;
}

export default function ImagesPage({ onClose }: ImagesPageProps) {
  const [images, setImages] = useState<UploadedImage[]>([]);
  const [dragActive, setDragActive] = useState(false);
  const [selectedImage, setSelectedImage] = useState<UploadedImage | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleDrag = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
    } else if (e.type === "dragleave") {
      setDragActive(false);
    }
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    
    const files = Array.from(e.dataTransfer.files).filter(file => 
      file.type.startsWith('image/')
    );
    
    handleFiles(files);
  };

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || []);
    handleFiles(files);
  };

  const handleFiles = (files: File[]) => {
    const newImages: UploadedImage[] = files.map(file => ({
      id: Date.now().toString() + Math.random(),
      file,
      preview: URL.createObjectURL(file),
      uploadedAt: new Date()
    }));
    
    setImages(prev => [...newImages, ...prev]);
  };

  const handleDelete = (id: string) => {
    const image = images.find(img => img.id === id);
    if (image) {
      URL.revokeObjectURL(image.preview);
    }
    setImages(images.filter(img => img.id !== id));
    if (selectedImage?.id === id) {
      setSelectedImage(null);
    }
  };

  const handleDownload = (image: UploadedImage) => {
    const link = document.createElement('a');
    link.href = image.preview;
    link.download = image.file.name;
    link.click();
  };

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
  };

  const formatDate = (date: Date) => {
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  return (
    <div className="h-screen flex flex-col bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
      {/* Header */}
      <div className="border-b border-slate-700/50 bg-slate-900/50 backdrop-blur-sm">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={onClose}
                className="p-2 rounded-lg bg-slate-800/50 hover:bg-slate-700/50 transition-all text-slate-100 group"
                title="Back to chat"
              >
                <HiChevronLeft size={20} className="group-hover:-translate-x-1 transition-transform" />
              </button>
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-600 to-purple-600 flex items-center justify-center shadow-lg">
                  <BsImages size={22} className="text-white" />
                </div>
                <div>
                  <h1 className="text-2xl font-bold text-slate-50">Images</h1>
                  <p className="text-sm text-slate-300">Upload and manage project images</p>
                </div>
              </div>
            </div>
            <button
              onClick={() => fileInputRef.current?.click()}
              className="flex items-center gap-2 px-4 py-2.5 bg-gradient-to-r from-violet-600 to-purple-600 hover:from-violet-500 hover:to-purple-500 text-white rounded-xl font-medium shadow-lg hover:shadow-xl transition-all"
            >
              <HiUpload size={20} />
              <span>Upload Images</span>
            </button>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto">
        <div className="max-w-7xl mx-auto px-6 py-8">
          {/* Stats */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
            <div className="bg-slate-800/40 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-300 text-sm font-medium">Total Images</p>
                  <p className="text-3xl font-bold text-slate-50 mt-1">{images.length}</p>
                </div>
                <div className="w-12 h-12 rounded-lg bg-violet-600/20 flex items-center justify-center">
                  <BsImages size={24} className="text-violet-400" />
                </div>
              </div>
            </div>
            <div className="bg-slate-800/40 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-300 text-sm font-medium">Total Size</p>
                  <p className="text-3xl font-bold text-slate-50 mt-1">
                    {formatFileSize(images.reduce((acc, img) => acc + img.file.size, 0))}
                  </p>
                </div>
                <div className="w-12 h-12 rounded-lg bg-purple-600/20 flex items-center justify-center">
                  <HiPhotograph size={24} className="text-purple-400" />
                </div>
              </div>
            </div>
            <div className="bg-slate-800/40 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-slate-300 text-sm font-medium">Recent Uploads</p>
                  <p className="text-3xl font-bold text-slate-50 mt-1">
                    {images.filter(img => {
                      const diff = Date.now() - img.uploadedAt.getTime();
                      return diff < 24 * 60 * 60 * 1000;
                    }).length}
                  </p>
                </div>
                <div className="w-12 h-12 rounded-lg bg-fuchsia-600/20 flex items-center justify-center">
                  <BsFileEarmarkImage size={24} className="text-fuchsia-400" />
                </div>
              </div>
            </div>
          </div>

          {/* Hidden File Input */}
          <input
            ref={fileInputRef}
            type="file"
            multiple
            accept="image/*"
            className="hidden"
            onChange={handleFileSelect}
          />

          {/* Upload Area */}
          {images.length === 0 && (
            <div
              onDragEnter={handleDrag}
              onDragLeave={handleDrag}
              onDragOver={handleDrag}
              onDrop={handleDrop}
              className={`
                border-4 border-dashed rounded-2xl p-16 text-center transition-all
                ${dragActive 
                  ? 'border-violet-500 bg-violet-900/20' 
                  : 'border-slate-700/50 hover:border-slate-600 bg-slate-800/20'
                }
              `}
            >
              <div className="w-24 h-24 rounded-2xl bg-gradient-to-br from-violet-600 to-purple-600 flex items-center justify-center mx-auto mb-6 shadow-xl">
                <HiUpload size={48} className="text-white" />
              </div>
              <h3 className="text-2xl font-bold text-slate-50 mb-2">Upload Images</h3>
              <p className="text-slate-300 mb-6">Drag and drop images here, or click to browse</p>
              <button
                onClick={() => fileInputRef.current?.click()}
                className="px-8 py-3 bg-gradient-to-r from-violet-600 to-purple-600 hover:from-violet-500 hover:to-purple-500 text-white rounded-xl font-semibold shadow-lg hover:shadow-xl transition-all"
              >
                Choose Images
              </button>
              <p className="text-sm text-slate-400 mt-4">Supports: JPG, PNG, GIF, WebP</p>
            </div>
          )}

          {/* Images Grid */}
          {images.length > 0 && (
            <>
              <div
                onDragEnter={handleDrag}
                onDragLeave={handleDrag}
                onDragOver={handleDrag}
                onDrop={handleDrop}
                className={`
                  mb-6 border-2 border-dashed rounded-xl p-8 text-center transition-all
                  ${dragActive 
                    ? 'border-violet-500 bg-violet-900/20' 
                    : 'border-slate-700/50 hover:border-slate-600 bg-slate-800/20'
                  }
                `}
              >
                <p className="text-slate-300">
                  Drag and drop more images here, or{' '}
                  <button
                    onClick={() => fileInputRef.current?.click()}
                    className="text-violet-400 hover:text-violet-300 font-semibold underline"
                  >
                    browse
                  </button>
                </p>
              </div>

              <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                {images.map((image) => (
                  <div
                    key={image.id}
                    className="group relative bg-slate-800/40 backdrop-blur-sm border-2 border-slate-700/50 hover:border-violet-500 rounded-xl overflow-hidden transition-all hover:shadow-xl hover:shadow-violet-900/50 cursor-pointer"
                    onClick={() => setSelectedImage(image)}
                  >
                    <div className="aspect-square relative">
                      <img
                        src={image.preview}
                        alt={image.file.name}
                        className="w-full h-full object-cover"
                      />
                      <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />
                    </div>
                    <div className="p-3">
                      <p className="text-sm font-medium text-slate-100 truncate mb-1">
                        {image.file.name}
                      </p>
                      <div className="flex items-center justify-between text-xs text-slate-400">
                        <span>{formatFileSize(image.file.size)}</span>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDelete(image.id);
                          }}
                          className="p-1 hover:bg-red-600 rounded transition-colors text-slate-300 hover:text-white"
                        >
                          <HiTrash size={14} />
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </>
          )}
        </div>
      </div>

      {/* Image Preview Modal */}
      {selectedImage && (
        <div
          className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
          onClick={() => setSelectedImage(null)}
        >
          <div
            className="bg-gradient-to-br from-slate-900 to-slate-800 border-2 border-violet-600 rounded-2xl max-w-4xl w-full max-h-[90vh] overflow-hidden shadow-2xl"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex items-center justify-between p-4 border-b border-slate-700">
              <h3 className="text-lg font-bold text-slate-50 truncate">
                {selectedImage.file.name}
              </h3>
              <div className="flex items-center gap-2">
                <button
                  onClick={() => handleDownload(selectedImage)}
                  className="p-2 rounded-lg bg-slate-800 hover:bg-slate-700 transition-colors text-slate-100"
                  title="Download"
                >
                  <HiDownload size={20} />
                </button>
                <button
                  onClick={() => {
                    handleDelete(selectedImage.id);
                    setSelectedImage(null);
                  }}
                  className="p-2 rounded-lg bg-red-600 hover:bg-red-700 transition-colors text-white"
                  title="Delete"
                >
                  <HiTrash size={20} />
                </button>
                <button
                  onClick={() => setSelectedImage(null)}
                  className="p-2 rounded-lg bg-slate-800 hover:bg-slate-700 transition-colors text-slate-100"
                >
                  <HiX size={20} />
                </button>
              </div>
            </div>
            <div className="p-4">
              <img
                src={selectedImage.preview}
                alt={selectedImage.file.name}
                className="w-full h-auto max-h-[70vh] object-contain rounded-lg"
              />
              <div className="mt-4 flex items-center justify-between text-sm text-slate-300">
                <span>{formatFileSize(selectedImage.file.size)}</span>
                <span>{formatDate(selectedImage.uploadedAt)}</span>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}