'use client';
//MainLayout.tsx
import { useState } from 'react';
import Sidebar from './Sidebar';
import Chat from './Chat';
import ProjectsPage from './ProjectsPage';
import ImagesPage from './ImagesPage';

const mockModules = [
  {
    id: 'gpt-4',
    display_name: 'GPT-4',
    description: 'Most capable model',
    installed: true,
  },
  {
    id: 'gpt-3.5',
    display_name: 'GPT-3.5 Turbo',
    description: 'Fast and efficient',
    installed: true,
  },
  {
    id: 'claude',
    display_name: 'Claude 3',
    description: 'Anthropic\'s AI',
    installed: true,
  },
  {
    id: 'gemini',
    display_name: 'Gemini Pro',
    description: 'Google\'s AI',
    installed: true,
  },
];

export default function MainLayout() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [chats, setChats] = useState<any[]>([]);
  const [activeChat, setActiveChat] = useState<any>(null);
  const [selectedModule, setSelectedModule] = useState(mockModules[0]);
  const [currentView, setCurrentView] = useState<'chat' | 'projects' | 'images'>('chat');

  const handleToggleSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen);
  };

  const handleNewChat = () => {
    setActiveChat(null);
    setCurrentView('chat');
  };

  const handleSelectChat = (chat: any) => {
    setActiveChat(chat);
    setCurrentView('chat');
  };

  const handleSelectModule = (module: any) => {
    setSelectedModule(module);
  };

  const handleSaveChat = (chat: any) => {
    setChats(prevChats => {
      const existingIndex = prevChats.findIndex(c => c.id === chat.id);
      if (existingIndex >= 0) {
        const updatedChats = [...prevChats];
        updatedChats[existingIndex] = chat;
        return updatedChats;
      } else {
        return [chat, ...prevChats];
      }
    });
    setActiveChat(chat);
  };

  const handleOpenImages = () => {
    setCurrentView('images');
  };

  const handleOpenProjects = () => {
    setCurrentView('projects');
  };

  const handleCloseSpecialView = () => {
    setCurrentView('chat');
  };

  return (
    <div className="flex h-screen overflow-hidden bg-slate-950">
      {/* Show sidebar only in chat view */}
      {currentView === 'chat' && (
        <Sidebar
          isOpen={isSidebarOpen}
          onToggle={handleToggleSidebar}
          chats={chats}
          activeChat={activeChat}
          onNewChat={handleNewChat}
          onSelectChat={handleSelectChat}
          modules={mockModules}
          selectedModule={selectedModule}
          onSelectModule={handleSelectModule}
          onOpenImages={handleOpenImages}
          onOpenProjects={handleOpenProjects}
        />
      )}

      {/* Main Content Area */}
      <div className="flex-1 overflow-hidden">
        {currentView === 'chat' && (
          <Chat
            toggleComponentVisibility={handleToggleSidebar}
            selectedModule={selectedModule}
            onSelectModule={handleSelectModule}
            modules={mockModules}
            activeChat={activeChat}
            onSaveChat={handleSaveChat}
          />
        )}
        
        {currentView === 'projects' && (
          <ProjectsPage onClose={handleCloseSpecialView} />
        )}
        
        {currentView === 'images' && (
          <ImagesPage onClose={handleCloseSpecialView} />
        )}
      </div>
    </div>
  );
}